import * as React from "react"
import cl from 'classnames'
import './index.css';

const DEFAULT_TYPE = 'normal'

const allowedTypes = new Set([
  DEFAULT_TYPE,
  'small',
  'narrow',
]);

export default function Container({ children, type = 'normal' }) {
  const modType = allowedTypes.has(type) ? type : DEFAULT_TYPE;
  const mods = {
    [`container--${modType}`]: true,
  };

  return (
    <div className={cl('container', mods)}>
      {children}
    </div>
  )
}
