import React from "react"
import cl from 'classnames'
import { Link } from "gatsby"
import "./index.css"

const DEFAULT_TYPE = 'default';
const DEFAULT_INVERTER = 'default-inverted';

const allowedTypes = new Set([
  DEFAULT_TYPE,
  DEFAULT_INVERTER,
  'button',
  'icon',
  'wrapper',
])

export default function StyledLink({
  href,
  alt,
  onClick,
  children,
  type = DEFAULT_TYPE,
  isExternal,
  className,
  target,
  isBold,
}) {
  const modType = allowedTypes.has(type) ? type : DEFAULT_TYPE;

  const mods = {
    [`link--${modType}`]: modType,
    'link--bold': !!isBold,
    [className]: !!className,
  };

  if (modType === DEFAULT_INVERTER) {
    mods[`link--${DEFAULT_TYPE}`] = true;
  }

  return (
    isExternal
    ? <a className={cl('link', mods)} href={href} alt={alt} onClick={onClick} target={target}>
      {children}
    </a>
    : <Link className={cl('link', mods)} to={href} alt={alt} onClick={onClick} target={target}>
        {children}
      </Link>
  )
}
